:root {
    --primary-color: #F0D85A;
    --secondary-color: #1A1A1A;
    --tertiary-color: #4d4d4d;
    --divider-color: #1B1B1B;
    --font-size: 1vw;
    --font-size-2: 0.4vw;
    --font-family: 'Poppins', sans-serif;
}

* {
    box-sizing: border-box;
    overflow: hidden;
}

#root {
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
}

.App {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.Home {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}

header {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 10%;
    align-items: center;
}

main {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

body {
    height: 60%;
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: black;
}

.dashboard {
    color: #F0D85A;
    color: var(--primary-color);
    font-family: 'Poppins', sans-serif;
    font-family: var(--font-family);
    letter-spacing: 1px;
    margin-left: 22.5%;
    font-size: 2vw;
}

.dashboard-user {
    color: white;
    font-family: 'Poppins', sans-serif;
    font-family: var(--font-family);
    letter-spacing: 1px;
    margin-left: 17%;
    font-size: .75vw;
}

.logo {
    border-radius: 35%;
    width: 5%;
}

.logo:hover {
    cursor: pointer;
}

form {
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

#search {
    background-color: #1A1A1A;
    background-color: var(--secondary-color);
    border-style: none;
    border-radius: 30px;
    width: 100%;
    height: 60%;
    padding: 10px;
    color: white;
    font-size: 1vw;
    font-size: var(--font-size);
    margin-left: 10%;
}

input[type="text"]:focus {
    outline: none;
}

.data {
    background-color: #1A1A1A;
    background-color: var(--secondary-color);
    width: 100%;
    height: 95%;
    float: right;
    border-radius: 30px;
    margin: 15px;
    display: flex;
    flex-direction: column;
}

.top,
.bottom {
    display: flex;
    width: 100%;
    align-items: center;
}

.top {
    height: 35%;
    flex-direction: row;
    justify-content: space-evenly;
}

.bottom {
    height: 65%;
}

.no-id {
    text-align: center;
}

.favorites {
    background-color: #F0D85A;
    background-color: var(--primary-color);
    width: 35%;
    height: 90%;
    border-radius: 30px;
    margin: 15px;
    display: inline-block;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.favorite-top {
    width: 100%;
    height: 15%;
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    justify-content: center;
}

.favorite-coins {
    overflow-y: scroll;
    box-sizing: border-box;
    height: 85%;;
}

.favorite-title {
    color: black;
    font-family: 'Poppins', sans-serif;
    font-family: var(--font-family);
    letter-spacing: 1px;
    font-size: 1vw;
    font-size: var(--font-size);
    padding-left: 15px;
}

.coin-button {
    width: 100%;
    height: 20%;
    flex-direction: row;
    font-family: 'Poppins', sans-serif;
    font-family: var(--font-family);
    font-size: 0.4vw;
    font-size: var(--font-size-2);
    background-color: #F0D85A;
    background-color: var(--primary-color);
    border: none;
    padding: 0;

}

.coin-button:hover {
    cursor: pointer;
    width: 100%;
    height: 20%;
}


.graph {
    background-color: black;
    border-radius: 30px;
    width: 65%;
    height: 90%;
    margin: 15px;
}

.graph-top {
    width: 100%;
    height: 15%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.graph-title {
    color: white;
    font-family: 'Poppins', sans-serif;
    font-family: var(--font-family);
    letter-spacing: 1px;
    font-size: 1vw;
    font-size: var(--font-size);
    padding-left: 15px;
}

.graph-add-favorite {
    background-color: #F0D85A;
    background-color: var(--primary-color);
    width: 50px;
    height: 50%;
    border-radius: 30px;
    margin-left: 10px;
    cursor: pointer;
    border: none;
}

.divider {
    background-color: #1B1B1B;
    background-color: var(--divider-color);
    width: 100%;
    height: 2px;
    border-radius: 30px;
}

.trash {
    width: 50%;
    height:50%;
    cursor: pointer;
}

.heart {
    height: 50%;
    fill:white;
    cursor: pointer;
    transition: 500ms;
    margin-left: 20px;
}

.heart.active {
    fill: red;
    cursor: pointer;
    transition: 250ms;
}

.coin {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    align-items: center;
    justify-content: space-around;
    cursor: crosshair;
}

.coin-delete-favorite {
    background-color: var(--tertiary-color);
    width: 50px;
    height: 50%;
    border-radius: 30px;
    border: none;
}

.coin:hover {
    box-sizing: border-box;
    background-color: rgb(151, 151, 151);
  }

.coin-image {
    object-fit: cover;
    width: 40px;
    cursor: pointer;
}

.coin-name {
    font-style: var();
}

.coin-symbol {
    color: var(--tertiary-color)
}

.coin-top {
    display: inline-block;
    justify-content: center;
    align-items: center;
}

.side-coin {
    display: inline-block;
    width: 12.5%;
}

.mid-coin {
    display: inline-block;
    width: 18.5%;
}

.price-change-7-days {
    background-color: black;
    text-align: center;
    color: green;
    border-radius: 30px;
    padding: 2px;
    font-size: .7vw
}

.end-coin {
    float: right;
    width: 7.5%;
}
.graph-data {
    padding-left: 1%;
    box-sizing: border-box;
    width: 100%;
    height: 85%;
}
.navbar {
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 10px;
    text-decoration: none;
}

.menu-bars {
    font-size: 2rem;
    background: none;
    color: grey;
    padding: 5px;
}

.menu-bars-link {
    text-decoration: none;
    color: inherit;
}

.menu-bars:hover {
    color: var(--primary-color);
    cursor: pointer;
}

.nav-menu {
    background-color: black;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    padding-top: 10px;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.navbar-link {
    text-decoration: none;
    color: inherit;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 8px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: var(--font-size);
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    border-radius: 4px;
    padding: 5px;
}

.nav-text a:hover {
    background-color: var(--primary-color);
    color:black;
}

.nav-menu-items {
    width: 100%;
    padding: 10px;
    margin: 0;
}

.navbar-toggle {
    background-color: #000000;
    left: 0;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}

.logout {
    margin-top: 600px;
    padding: 10px;
    display: flex;
    font-size: var(--font-size);
    justify-content: center;
    text-align: center;
    align-items: center;
    width: 95%;
    height: 100%;
    color: black;
    background-color: var(--primary-color);
    cursor: pointer;
    border: 2px;
    border-radius: 4px;
    border-color: white;
    border-style: solid;
}

.logout:hover {
    background-color: var(--tertiary-color);
    color: black;
}

.navbar-user {
    color: white;
}

.login-signup {
    width: 100%;
    margin: 0;
    flex-direction: column;
}

.login-signup a{
    text-decoration: none;
    color: #f5f5f5;
