}

.login-signup a:hover {
    background-color: var(--primary-color);
    color:black;
}
.gainer-button {
    background-color: black;
    height: 85%;
    width: 20%;
    border-radius: 30px;
    margin: 15px;
    color: white;
    cursor: pointer;
    border: none;
}

.gainer-button:active {
    border:none;
