.gainer {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.gainer-top {
    width: 100%;
    margin-top: 10px;
    height: 30%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
}

.gainer-image {
    width: 50px;
}

.gainer-button:active {
    border:none;
